.login_main{
    padding: 2%;
    margin-top: 2%;
}

.login_form{
    width : 35%;
    box-shadow: 0 0 2px black;
    padding: 1%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.637);
    padding-bottom: 2%;
}

.login_container {
    margin-bottom: 3%;
    width : 70%;
}

.form_input{
    padding : 5%;
    width : 100%;
    font-size: 1rem;
    border-radius: 20px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.form_btn{
    padding: 2%;
    width : 60%;
    margin-top: 3%;
    border-radius: 20px;
    font-size: 0.9rem;
    text-align: center;
    color: white;
    font-weight: bold;
    background-color: rgba(0, 0, 0, 0.764);
    cursor: pointer;
}

.form_btn:hover{
    background-color: rgba(0, 0, 0, 0.80);
}
